<template>
  <div>
    <a-table
      :columns="columns"
      :rowKey="record => record.idx"
      :dataSource="tableData"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
    </a-table>
  </div>
</template>

<script>
import {
  getSzHistoryData // 深股通历史数据
} from '@/api/index'
const columns = [
  { align: 'center', title: '日期', dataIndex: 'date' },
  { align: 'center', title: '当日资金流入', dataIndex: 'capitalInflows' },
  { align: 'center', title: '当日余额', dataIndex: 'balance' },
  { align: 'center', title: '历史资金累计流入', dataIndex: 'totalCapitalInflows' },
  { align: 'center', title: '当日成交静买额', dataIndex: 'netPurchase' },
  { align: 'center', title: '买入成交额', dataIndex: 'buyVolume' },
  { align: 'center', title: '卖出成交额', dataIndex: 'sellVolume' },
  { align: 'center', title: '领涨股', dataIndex: 'name' },
  { align: 'center', title: '领涨股涨跌幅', dataIndex: 'chgOfLedRising' },
  { align: 'center', title: '上证指数', dataIndex: 'indexs' },
  { align: 'center', title: '涨跌幅', dataIndex: 'chgOfIndex' }
]
export default {
  name: 'szHistroy',
  data() {
    return {
      tableData: [],
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0
      },
      loading: false,
      columns
    }
  },
  created() {
    this.getData()
  },
  methods: {
    handleTableChange(pagination, filters, sorter) {
      this.pagination = { ...pagination }
      this.getData()
    },
    getData() {
      let data = {
        pageNum: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.loading = true
      getSzHistoryData(data).then(res => {
        console.log('深股通历史数据')
        console.log(res)
        if (res.code === 200) {
          let obj = res.data.data
          for (let i in obj) {
            obj[i].idx = i
          }
          this.tableData = obj
          const pagination = { ...this.pagination }
          pagination.total = res.data.total
          this.pagination = pagination
        }
        this.loading = false
      })
    }
  }
}
</script>
